.button {
  font: inherit;
padding: 0.5rem 1.5rem;
border: 1px solid #fff;
border-radius: 4px;
background: #cfffc8;
color: #020202;
cursor: pointer;
margin-right: 1rem;
text-decoration: none;
display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #befc72;
  border-color: #a6ff68;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #f00d0d;
  border-color: #830000;
  color:white;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}