.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
  transition: all 0.2s;
}

.nav-links a:hover{
  background: #fff123c9;
  border-radius: 20px;
  border-color: #292929;
  color: #292929;

}
.nav-links a.active {
  color: #ffffff;
  border-bottom: 2px solid yellow;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  text-transform: uppercase;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover{
  background: #ff08089e;
color: #fff;
}
.nav-links button:active {
  background: #ff08089e;
color: #fff;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: white;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }
  
  .nav-links button:hover,
  .nav-links button:active {
    background: #ff08089e;
color: #fff;
  }
}
