.starter{
    height: 95vh;
    margin:0;
    background-image: linear-gradient(to right,rgba(121, 185, 121, 0.651),rgba(115, 209, 170, 0.685)),url("bg.jpg");
    background-size: cover;
    background-position: center;
    position: relative;
}
.starter-heading{
    font-size: 7rem;
    text-align: center;
    position: absolute;
    color:white;
    font-weight: bold;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
}
.starter-after{
    margin:5rem;
    text-align: center;
    font-size: 3rem;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}